































































































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class Stats extends Vue {
  stats: any[] = [];
  currentKicker: any = null;
  currentIndex: number = -1;
  filter: string = "";
  amount = 10.0;
  active = false;
  year = 0;
  selectableYears: number[] = [];

  async retrieveStats() {
    try {
      this.stats = (
        await DataService.getStats(this.active, this.filter, this.year)
      ).data;
    } catch (error) {
      console.error(error);
    }
  }

  refreshList() {
    this.retrieveStats();
    this.currentKicker = null;
    this.currentIndex = -1;
  }

  setActiveKicker(kicker: any, index: number) {
    this.currentKicker = kicker;
    this.currentIndex = index;
  }

  setActive(active: boolean) {
    this.active = active;
    this.retrieveStats();
  }

  setCurrentYear(current: number) {
    this.year = current;
    this.retrieveStats();
  }

  mounted() {
    for (let year = new Date().getFullYear()-1; year >= 2013; year--) {
      this.selectableYears.push(year);
    }
    this.retrieveStats();
  }
}
